import React, { useCallback, useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useDispatch } from 'react-redux';

import Header from './components/Header';
import Layout from './pages/Layout';
import Navigation from './components/Navigation';

import { API_RECALL_TIMER } from './services/helpers';
import { getPopularCoins } from './store/actions/actionHome';
import { getTrendingCoins } from './store/actions/actionSearch';

export interface Props {}

const useStyles = makeStyles((theme) => ({
  appContainer: {
    width: 360,
    height: 600,
    backgroundColor: '#F8F8F8',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-between',
  },
}));

export const App: React.FC<Props> = () => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const gatherData = useCallback(() => {
    dispatch(getPopularCoins());
    dispatch(getTrendingCoins());
  }, [dispatch]);

  const [value, setValue] = useState<string>('home');
  const handleChange = (event: any, newValue: string) => {
    setValue(newValue);
  };

  useEffect(() => {
    gatherData();
    const interval = setInterval(() => gatherData(), API_RECALL_TIMER);
    return () => clearInterval(interval);
  }, [dispatch, gatherData]);

  return (
    <div className={classes.appContainer}>
      <Header currentPage={value} />
      <Layout currentPage={value} />
      <Navigation currentPage={value} switchPage={handleChange} />
    </div>
  );
};

export default App;
